import React, { Component } from 'react'
import ajaxReq from '../../common/ajaxReq'
import msg from '../../common/msg'
import './index.scss'
import {
  Loading,
  Table,
  Button,
  Dialog,
  Input,
  Form,
  MessageBox,
  Upload,
  Pagination
} from 'element-react'

const source = {}
class Account extends Component {
  constructor(props) {
    super(props)
    this.state = {
      page: 1,
      page_size: 20,
      name: '',
      pid: '',
      params: {
        id: '',
        name: '',
        income: ''
      },
      columns: [
        {
          type: 'selection'
        },
        {
          label: '',
          prop: 'name',
          render: function(data) {
            return <span>{data.name || '--'}</span>
          }
        },
        {
          label: '',
          prop: 'income',
          width: 160
        },
        {
          label: '',
          prop: 'address',
          render: item => {
            return (
              <span>
                <Button
                  type="primary"
                  size="small"
                  onClick={this.openDialog.bind(this, item)}
                >
                  
                </Button>
                <Button
                  onClick={this.deleteItem.bind(this, [item.id])}
                  type="danger"
                  size="small"
                >
                  
                </Button>
              </span>
            )
          }
        }
      ],
      data: [],
      selected: [],
      count: 0,
      loading: true,
      dialogVisible: false,
      excelDialogVisible: false
    }
  }

  importExcel() {
    this.setState({
      excelDialogVisible: true
    })
  }
  beforeUpload(file) {
    let index = file.name.lastIndexOf('.')
    const ext = file.name.slice(index + 1).toLowerCase()
    const include = ['xls', 'xlsx'].includes(ext)
    if (!include) {
      msg('.xlsx  .xls', false, 2000)
    }
    return include
  }
  comfirmImport() {
    this.setState({
      excelDialogVisible: true
    })
  }

  uploadChange(file) {
    if (file.response.code !== 0) {
      msg(file.name + ' :' + file.response.msg, false, 10000)
    } else {
      msg(file.name + '', true, 3000)
      this.getList()
    }
  }

  openDialog({ id = 0, name = '', income }) {
    const params = {
      id,
      name,
      income
    }
    this.setState({ params, dialogVisible: true })
  }

  async deleteItem(ids) {
    await MessageBox.confirm(``, '', {
      type: 'warning'
    })
    this.setState({ loading: true })
    ajaxReq
      .call(this, {
        url: '/wage/delete',
        params: {
          id: ids
        }
      })
      .then(() => {
        msg('')
        this.setState({ dialogVisible: false, selected: [] }, this.getList)
      })
      .catch(() => {
        this.setState({ dialogVisible: false, loading: false })
      })
  }

  onSizeChange(page_size) {
    this.setState(
      {
        page_size
      },
      this.getList
    )
  }
  onCurrentChange(page) {
    this.setState(
      {
        page
      },
      this.getList
    )
  }

  getList(initId) {
    this.setState({ loading: true })
    const { pid, page, page_size, name } = this.state
    if (source.cancel) source.cancel('cancel')
    ajaxReq
      .call(this, {
        url: '/wage/get_list',
        params: { pid: initId || pid, page, page_size, name },
        can: source
      })
      .then(res => {
        const { list, count } = res.data
        this.setState({
          data: list,
          count,
          loading: false
        })
      })
      .catch(() => {
        this.setState({ dialogVisible: false, loading: false })
      })
  }

  addAccount = () => {
    let { pid } = this.state
    let { name, income, id } = this.state.params
    if (!name.trim()) {
      msg('', false)
      return
    }
    if (!income || income <= 0) {
      msg('', false)
      return
    }
    this.setState({ loading: true })
    ajaxReq
      .call(this, {
        url: '/wage/add',
        params: {
          pid,
          name,
          id,
          income
        }
      })
      .then(() => {
        if (id === 0) {
          msg('')
        } else {
          msg('')
        }
        this.setState({ dialogVisible: false })
        this.getList()
      })
      .catch(() => {
        this.setState({ dialogVisible: false, loading: false })
      })
  }
  componentWillMount() {
    const id = this.props.match.params.id
    this.setState({ pid: id })
    this.getList(id)
  }

  render() {
    return (
      <div className="account wage-content">
        <Loading loading={this.state.loading}>
          <div className="head">
            <Button
              icon="arrow-left"
              onClick={() => {
                this.props.history.go(-1)
              }}
            >
              
            </Button>
            {this.state.selected.length ? (
              <Button
                onClick={this.deleteItem.bind(this, this.state.selected)}
                type="danger"
              >
                
              </Button>
            ) : null}
            <div className="center">
              <Input
                value={this.state.name}
                placeholder=""
                onChange={value => {
                  this.setState(
                    {
                      name: value,
                      page: 1
                    },
                    this.getList
                  )
                }}
              ></Input>
            </div>
            <div className="right">
              <Button
                type="primary"
                icon="edit"
                onClick={this.openDialog.bind(this)}
              >
                
              </Button>
              <Button icon="upload" onClick={this.importExcel.bind(this)}>
                
              </Button>
            </div>
          </div>

          <div className="table-container">
            <Table
              height="100%"
              columns={this.state.columns}
              data={this.state.data}
              border={true}
              highlightCurrentRow={true}
              onSelectChange={selection => {
                const selected = selection.map(item => item.id)
                this.setState({ selected })
              }}
            />
          </div>
          <div className="footer">
            <Pagination
              layout="total, sizes, prev, pager, next, jumper"
              total={this.state.count}
              pageSizes={[20, 50, 100]}
              pageSize={this.state.page_size}
              currentPage={this.state.page}
              onSizeChange={this.onSizeChange.bind(this)}
              onCurrentChange={this.onCurrentChange.bind(this)}
            />
          </div>
          <Dialog
            className="add-info"
            title={this.state.params.id ? '' : ''}
            visible={this.state.dialogVisible}
            closeOnClickModal={false}
            onCancel={() => this.setState({ dialogVisible: false })}
          >
            <Loading loading={this.state.loading && this.state.dialogVisible}>
              <Dialog.Body>
                <Form labelWidth="80">
                  <Form.Item label="">
                    <Input
                      placeholder=""
                      value={this.state.params.name}
                      onChange={value => {
                        this.setState({
                          params: { ...this.state.params, name: value }
                        })
                      }}
                    ></Input>
                  </Form.Item>
                  <Form.Item label="">
                    <Input
                      placeholder=""
                      type="number"
                      value={this.state.params.income}
                      onChange={value => {
                        this.setState({
                          params: { ...this.state.params, income: value }
                        })
                      }}
                    ></Input>
                  </Form.Item>
                </Form>
              </Dialog.Body>

              <Dialog.Footer className="dialog-footer">
                <Button onClick={() => this.setState({ dialogVisible: false })}>
                   
                </Button>
                <Button type="primary" onClick={this.addAccount.bind(this)}>
                   
                </Button>
              </Dialog.Footer>
            </Loading>
          </Dialog>
          <Dialog
            className="import-dialog"
            title=""
            visible={this.state.excelDialogVisible}
            closeOnClickModal={false}
            onCancel={() => this.setState({ excelDialogVisible: false })}
          >
            <Loading
              loading={this.state.loading && this.state.excelDialogVisible}
            >
              <Dialog.Body>
                <Upload
                  className="upload-demo"
                  drag
                  accept=".csv, application/vnd.openxmlformats-officedocument.spreadsheetml.sheet, application/vnd.ms-excel"
                  data={{ pid: this.state.pid }}
                  action="/wage/import"
                  multiple
                  beforeUpload={this.beforeUpload.bind(this)}
                  onChange={this.uploadChange.bind(this)}
                  tip={<div className="el-upload__tip">excel</div>}
                >
                  <i className="el-icon-upload"></i>
                  <div className="el-upload__text">
                    <em></em>
                  </div>
                </Upload>
              </Dialog.Body>
            </Loading>
          </Dialog>
        </Loading>
      </div>
    )
  }
}

export default Account
